*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

main{
    min-height: 100vh;
    padding: 25px;
}

.app{
    background-image: url('./assets/cold.jpg');
    background-size: cover;
    background-position: center;
    transition: 0.4s ease;
}

.app.warm{
    background-image: url('./assets/warm.jpg');
    background-size: cover;
    background-position: center;
    transition: 0.4s ease;
}

.app.normal{
    background-image: url('./assets/normal.jpg');
    background-size: cover;
    background-position: center;
    transition: 0.4s ease;
}

.app.cool{
    background-image: url('./assets/20-25.jpg');
    background-size: cover;
    background-position: center;
    transition: 0.4s ease;
}

.search-box{
    position: relative;
    width: 70%;
    top: 30px;
    margin: 0 0 75px;
    left: 350px
}

.search-box .search-bar{
    display: block;
    
    width: 75%;
    padding: 15px;
    appearance: none;
    background: none;
    outline: none;
    background-color: rgba(255,255,255,0.5);
    border-radius: 16px 16px 16px 16px;
    margin-top: -25px;
    box-shadow: 0px 5px rgba(0,0,0,0.2);
    color: #313131;
    font-size: 20px;
    transition: 0.4s ease;
}

.search-box .search-bar:focus{
    background-color: rgba(255,255,255,0.75);
}

.location-box .location{
    color: #fff;
    font-size: 32px;
    font-weight: 500;
    text-align: center;
    text-shadow: 3px 3px rgba(50,50,70,0.75);
}

.location-box .date{
    color: #fff;
    text-align: center;
    font-size: 20px;
    font-weight: 300;
    font-style: italic;
    text-shadow: 2px 2px rgba(50,50,70,0.5);
}

.weather-box{
    text-align: center;
}

.weather-box .temp{
    position: relative;
    display: inline-block;
    margin: 30px auto;
    background-color: rgba(255,255,255,0.2);
    border-radius: 16px;
    padding: 15px 25px;
    color: #fff;
    font-size: 80px;
    text-shadow: 3px 6px rgba(50,50,70,0.5);
    box-shadow: 3px 6px rgba(0,0,0,0.2);
}

.weather-box .weather{
    color: #fff;
    text-shadow: 3px 3px rgba(50,50,70,0.5);
    font-size: 48px;
    font-weight: 700;
}